import { AlertTriangle, MapPin, ExternalLink, Calendar } from 'lucide-react';
import { clsx, type ClassValue } from 'clsx';
import { twMerge } from 'tailwind-merge';
import type { Threat } from '../types';

function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

interface ThreatCardProps {
  threat: Threat;
  compact?: boolean;
}

const severityConfig = {
  critical: {
    color: 'border-red-500/30 bg-red-500/5',
    badge: 'bg-red-500/10 text-red-400',
    iconColor: 'text-red-400'
  },
  high: {
    color: 'border-amber-500/30 bg-amber-500/5',
    badge: 'bg-amber-500/10 text-amber-400',
    iconColor: 'text-amber-400'
  },
  medium: {
    color: 'border-blue-500/30 bg-blue-500/5',
    badge: 'bg-blue-500/10 text-blue-400',
    iconColor: 'text-blue-400'
  },
  low: {
    color: 'border-gray-500/30 bg-gray-500/5',
    badge: 'bg-gray-500/10 text-gray-400',
    iconColor: 'text-gray-400'
  }
};

const typeLabels: Record<string, string> = {
  new_license: 'New License',
  violation: 'Compliance Issue',
  competitor_opening: 'Competitor Opening',
  regulatory_change: 'Regulatory Change',
  price_pressure: 'Price Pressure'
};

export function ThreatCard({ threat, compact = false }: ThreatCardProps) {
  const severity = severityConfig[threat.severity];
  
  if (compact) {
    return (
      <div className={cn(
        'rounded-lg border p-4 transition-colors hover:bg-gray-800/30',
        severity.color
      )}>
        <div className="flex items-start gap-3">
          <AlertTriangle className={cn('mt-0.5 h-5 w-5 flex-shrink-0', severity.iconColor)} />
          <div className="flex-1 min-w-0">
            <div className="flex items-center gap-2">
              <span className={cn('rounded-full px-2 py-0.5 text-xs font-medium', severity.badge)}>
                {threat.severity}
              </span>
              <span className="text-xs text-gray-500">{typeLabels[threat.type]}</span>
            </div>
            <h4 className="mt-1 font-medium text-gray-100 truncate">{threat.entity_name}</h4>
            <p className="mt-0.5 text-sm text-gray-400 line-clamp-2">{threat.description}</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={cn(
      'rounded-lg border p-5 transition-colors hover:bg-gray-800/30',
      severity.color
    )}>
      <div className="flex items-start justify-between gap-4">
        <div className="flex items-start gap-3">
          <div className={cn('mt-1 rounded-lg bg-gray-800 p-2', severity.iconColor)}>
            <AlertTriangle size={20} />
          </div>
          <div>
            <div className="flex items-center gap-2 flex-wrap">
              <span className={cn('rounded-full px-2.5 py-1 text-xs font-medium uppercase tracking-wide', severity.badge)}>
                {threat.severity}
              </span>
              <span className="text-xs text-gray-500">{typeLabels[threat.type]}</span>
            </div>
            <h3 className="mt-2 text-lg font-semibold text-gray-100">{threat.entity_name}</h3>
            {threat.license_number && (
              <p className="text-sm text-gray-500">License: {threat.license_number}</p>
            )}
          </div>
        </div>
        <div className="flex items-center gap-1 text-sm text-gray-500">
          <Calendar size={14} />
          {new Date(threat.published_at).toLocaleDateString()}
        </div>
      </div>

      <p className="mt-4 text-sm text-gray-300 leading-relaxed">{threat.description}</p>

      <div className="mt-4 flex items-center gap-4 text-sm">
        {threat.radius_miles && (
          <div className="flex items-center gap-1.5 text-gray-400">
            <MapPin size={14} />
            <span>{threat.radius_miles} miles away</span>
          </div>
        )}
        {threat.source_url && (
          <a 
            href={threat.source_url}
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center gap-1.5 text-glass-400 hover:text-glass-300 transition-colors"
          >
            <ExternalLink size={14} />
            <span>View Source</span>
          </a>
        )}
      </div>
    </div>
  );
}
