import { useState } from 'react';
import { NavLink, useLocation } from 'react-router-dom';
import {
  LayoutDashboard,
  TrendingDown,
  Radar,
  Lightbulb,
  Bell,
  Settings,
  LogOut,
  Menu,
  X
} from 'lucide-react';
import { clsx, type ClassValue } from 'clsx';
import { twMerge } from 'tailwind-merge';

function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

interface SidebarProps {
  userName?: string;
  operatorName?: string;
}

const navItems = [
  { path: '/', icon: LayoutDashboard, label: 'Morning Brief' },
  { path: '/prices', icon: TrendingDown, label: 'Price Wire' },
  { path: '/threats', icon: Radar, label: 'Threat Radar' },
  { path: '/recommendations', icon: Lightbulb, label: 'Recommendations' },
  { path: '/alerts', icon: Bell, label: 'Alerts' },
];

export function Sidebar({ userName = 'User', operatorName = 'Operator' }: SidebarProps) {
  const [isOpen, setIsOpen] = useState(false);
  const location = useLocation();

  const toggleSidebar = () => setIsOpen(!isOpen);
  const closeSidebar = () => setIsOpen(false);

  return (
    <>
      {/* Mobile Menu Button */}
      <button
        onClick={toggleSidebar}
        className="fixed left-4 top-4 z-50 rounded-lg bg-gray-800 p-2 text-gray-300 lg:hidden hover:bg-gray-700"
      >
        {isOpen ? <X size={24} /> : <Menu size={24} />}
      </button>

      {/* Overlay */}
      {isOpen && (
        <div
          className="fixed inset-0 z-40 bg-black/50 lg:hidden"
          onClick={closeSidebar}
        />
      )}

      {/* Sidebar */}
      <aside
        className={cn(
          'fixed left-0 top-0 z-40 h-screen w-64 bg-gray-900 border-r border-gray-800 transition-transform duration-300 lg:translate-x-0',
          isOpen ? 'translate-x-0' : '-translate-x-full'
        )}
      >
        {/* Logo */}
        <div className="flex h-16 items-center border-b border-gray-800 px-6">
          <div className="flex items-center gap-2">
            <div className="h-8 w-8 rounded-lg bg-glass-400 flex items-center justify-center">
              <span className="text-lg font-bold text-gray-950">G</span>
            </div>
            <span className="text-xl font-bold text-gray-100">GLASS</span>
          </div>
        </div>

        {/* User Info */}
        <div className="border-b border-gray-800 px-6 py-4">
          <p className="text-sm font-medium text-gray-100">{userName}</p>
          <p className="text-xs text-gray-500">{operatorName}</p>
        </div>

        {/* Navigation */}
        <nav className="flex-1 space-y-1 px-4 py-4">
          {navItems.map((item) => {
            const Icon = item.icon;
            const isActive = location.pathname === item.path;
            
            return (
              <NavLink
                key={item.path}
                to={item.path}
                onClick={closeSidebar}
                className={cn(
                  'flex items-center gap-3 rounded-lg px-3 py-2.5 text-sm font-medium transition-colors',
                  isActive
                    ? 'bg-glass-400/10 text-glass-400'
                    : 'text-gray-400 hover:bg-gray-800 hover:text-gray-100'
                )}
              >
                <Icon size={18} />
                {item.label}
              </NavLink>
            );
          })}
        </nav>

        {/* Bottom Actions */}
        <div className="absolute bottom-0 left-0 right-0 border-t border-gray-800 p-4">
          <NavLink
            to="/settings"
            onClick={closeSidebar}
            className={cn(
              'flex items-center gap-3 rounded-lg px-3 py-2.5 text-sm font-medium transition-colors',
              location.pathname === '/settings'
                ? 'bg-glass-400/10 text-glass-400'
                : 'text-gray-400 hover:bg-gray-800 hover:text-gray-100'
            )}
          >
            <Settings size={18} />
            Settings
          </NavLink>
          <button
            onClick={() => {
              // Handle logout
              window.location.href = '/login';
            }}
            className="mt-1 flex w-full items-center gap-3 rounded-lg px-3 py-2.5 text-sm font-medium text-gray-400 transition-colors hover:bg-gray-800 hover:text-gray-100"
          >
            <LogOut size={18} />
            Sign Out
          </button>
        </div>
      </aside>
    </>
  );
}
