import { Check, X, TrendingUp, DollarSign, Package } from 'lucide-react';
import { clsx, type ClassValue } from 'clsx';
import { twMerge } from 'tailwind-merge';
import type { Recommendation } from '../types';

function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

interface RecommendationCardProps {
  recommendation: Recommendation;
  onApply?: (id: string) => void;
  onDismiss?: (id: string) => void;
  isApplying?: boolean;
  isDismissing?: boolean;
  compact?: boolean;
}

const statusConfig = {
  pending: {
    badge: 'bg-glass-400/10 text-glass-400',
    border: 'border-glass-400/20'
  },
  applied: {
    badge: 'bg-emerald-500/10 text-emerald-400',
    border: 'border-emerald-500/20'
  },
  dismissed: {
    badge: 'bg-gray-500/10 text-gray-400',
    border: 'border-gray-500/20'
  }
};

export function RecommendationCard({
  recommendation,
  onApply,
  onDismiss,
  isApplying,
  isDismissing,
  compact = false
}: RecommendationCardProps) {
  const status = statusConfig[recommendation.status];
  const priceChange = recommendation.recommended_price - recommendation.current_price;
  const percentChange = (priceChange / recommendation.current_price) * 100;
  const isIncrease = priceChange > 0;

  if (compact) {
    return (
      <div className={cn(
        'rounded-lg border bg-gray-900 p-4',
        status.border
      )}>
        <div className="flex items-start justify-between">
          <div>
            <div className="flex items-center gap-2">
              <span className={cn('rounded-full px-2 py-0.5 text-xs font-medium', status.badge)}>
                {recommendation.status}
              </span>
              <span className="text-xs text-gray-500">
                {Math.round(recommendation.confidence * 100)}% confidence
              </span>
            </div>
            <h4 className="mt-2 font-medium text-gray-100">{recommendation.product.name}</h4>
          </div>
          <div className="text-right">
            <p className="text-sm text-gray-500">${recommendation.current_price.toFixed(2)}</p>
            <p className={cn(
              'text-lg font-semibold',
              isIncrease ? 'text-emerald-400' : 'text-glass-400'
            )}>
              ${recommendation.recommended_price.toFixed(2)}
            </p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={cn(
      'rounded-lg border bg-gray-900 p-5',
      status.border
    )}>
      <div className="flex items-start justify-between gap-4">
        <div className="flex items-start gap-3">
          <div className="mt-1 rounded-lg bg-gray-800 p-2 text-glass-400">
            <TrendingUp size={20} />
          </div>
          <div>
            <div className="flex items-center gap-2 flex-wrap">
              <span className={cn('rounded-full px-2.5 py-1 text-xs font-medium uppercase tracking-wide', status.badge)}>
                {recommendation.status}
              </span>
              <span className="text-xs text-gray-500">
                {Math.round(recommendation.confidence * 100)}% confidence
              </span>
            </div>
            <h3 className="mt-2 text-lg font-semibold text-gray-100">{recommendation.product.name}</h3>
            <p className="text-sm text-gray-500">{recommendation.product.brand}  {recommendation.product.sku}</p>
          </div>
        </div>

        {/* Price Change */}
        <div className="text-right">
          <p className="text-sm text-gray-500">Current Price</p>
          <p className="text-xl font-bold text-gray-100">${recommendation.current_price.toFixed(2)}</p>
          <p className="text-sm text-gray-500 mt-1">Recommended</p>
          <p className={cn(
            'text-xl font-bold',
            isIncrease ? 'text-emerald-400' : 'text-glass-400'
          )}>
            ${recommendation.recommended_price.toFixed(2)}
          </p>
          <p className={cn(
            'text-sm font-medium',
            isIncrease ? 'text-emerald-400' : 'text-glass-400'
          )}>
            {isIncrease ? '+' : ''}{percentChange.toFixed(1)}%
          </p>
        </div>
      </div>

      {/* Impact Metrics */}
      <div className="mt-4 grid grid-cols-2 gap-4 border-t border-gray-800 pt-4 sm:grid-cols-3">
        <div className="flex items-center gap-2">
          <DollarSign size={16} className="text-gray-500" />
          <div>
            <p className="text-xs text-gray-500">Expected Margin</p>
            <p className={cn(
              'font-medium',
              recommendation.expected_margin_delta >= 0 ? 'text-emerald-400' : 'text-red-400'
            )}>
              {recommendation.expected_margin_delta >= 0 ? '+' : ''}
              ${recommendation.expected_margin_delta.toFixed(0)}/week
            </p>
          </div>
        </div>
        <div className="flex items-center gap-2">
          <Package size={16} className="text-gray-500" />
          <div>
            <p className="text-xs text-gray-500">Volume Impact</p>
            <p className={cn(
              'font-medium',
              recommendation.expected_unit_delta >= 0 ? 'text-emerald-400' : 'text-amber-400'
            )}>
              {recommendation.expected_unit_delta >= 0 ? '+' : ''}
              {(recommendation.expected_unit_delta * 100).toFixed(0)}%
            </p>
          </div>
        </div>
      </div>

      {/* Rationale */}
      <div className="mt-4 rounded-lg bg-gray-800/50 p-3">
        <p className="text-sm text-gray-300">{recommendation.rationale}</p>
      </div>

      {/* Actions */}
      {recommendation.status === 'pending' && onApply && onDismiss && (
        <div className="mt-4 flex items-center justify-end gap-3">
          <button
            onClick={() => onDismiss(recommendation.id)}
            disabled={isDismissing || isApplying}
            className="flex items-center gap-1.5 rounded-lg border border-gray-700 bg-gray-800 px-4 py-2 text-sm font-medium text-gray-300 transition-colors hover:bg-gray-700 disabled:opacity-50"
          >
            <X size={16} />
            {isDismissing ? 'Dismissing...' : 'Dismiss'}
          </button>
          <button
            onClick={() => onApply(recommendation.id)}
            disabled={isApplying || isDismissing}
            className="flex items-center gap-1.5 rounded-lg bg-glass-400 px-4 py-2 text-sm font-medium text-gray-950 transition-colors hover:bg-glass-300 disabled:opacity-50"
          >
            <Check size={16} />
            {isApplying ? 'Applying...' : 'Apply'}
          </button>
        </div>
      )}
    </div>
  );
}
