import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Eye, EyeOff, Lock, Mail } from 'lucide-react';
import { api } from '../api/client';

export default function Login() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setError('');
    setIsLoading(true);

    try {
      const response = await api.login({ email, password });
      
      // Store tokens in localStorage
      localStorage.setItem('glass_access_token', response.tokens.access_token);
      localStorage.setItem('glass_refresh_token', response.tokens.refresh_token);
      
      // Set token for API client
      api.setToken(response.tokens.access_token);
      
      // Redirect to dashboard
      navigate('/');
    } catch (err) {
      setIsLoading(false);
      setError(err instanceof Error ? err.message : 'Login failed. Please try again.');
    }
  };

  return (
    <div className="flex min-h-screen items-center justify-center bg-gray-950 px-4">
      <div className="w-full max-w-md">
        <div className="rounded-lg border border-gray-800 bg-gray-900 p-8">
          <div className="text-center">
            <div className="mx-auto flex h-12 w-12 items-center justify-center rounded-lg bg-[#00D4AA]">
              <span className="text-2xl font-bold text-gray-950">G</span>
            </div>
            <h1 className="mt-4 text-2xl font-bold text-gray-100">Welcome to GLASS</h1>
            <p className="mt-1 text-sm text-gray-400">
              Operating System for Massachusetts Cannabis Operators
            </p>
          </div>

          {error && (
            <div className="mt-4 rounded-lg border border-red-500/30 bg-red-500/10 p-3 text-sm text-red-400">
              {error}
            </div>
          )}

          <form onSubmit={handleSubmit} className="mt-6 space-y-4">
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-300">
                Email address
              </label>
              <div className="relative mt-1">
                <Mail className="absolute left-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-500" />
                <input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="w-full rounded-lg border border-gray-700 bg-gray-800 py-2.5 pl-10 pr-4 text-gray-100 placeholder:text-gray-500 focus:border-[#00D4AA] focus:outline-none focus:ring-1 focus:ring-[#00D4AA]"
                  placeholder="you@company.com"
                />
              </div>
            </div>

            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-300">
                Password
              </label>
              <div className="relative mt-1">
                <Lock className="absolute left-3 top-1/2 h-5 w-5 -translate-y-1/2 text-gray-500" />
                <input
                  id="password"
                  type={showPassword ? 'text' : 'password'}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full rounded-lg border border-gray-700 bg-gray-800 py-2.5 pl-10 pr-10 text-gray-100 placeholder:text-gray-500 focus:border-[#00D4AA] focus:outline-none focus:ring-1 focus:ring-[#00D4AA]"
                  placeholder=""
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-500 hover:text-gray-300"
                >
                  {showPassword ? (
                    <EyeOff className="h-5 w-5" />
                  ) : (
                    <Eye className="h-5 w-5" />
                  )}
                </button>
              </div>
            </div>

            <div className="flex items-center justify-between text-sm">
              <label className="flex items-center gap-2">
                <input
                  type="checkbox"
                  className="rounded border-gray-700 bg-gray-800 text-[#00D4AA] focus:ring-[#00D4AA]"
                />
                <span className="text-gray-400">Remember me</span>
              </label>
              <a href="#" className="text-[#00D4AA] hover:underline">
                Forgot password?
              </a>
            </div>

            <button
              type="submit"
              disabled={isLoading}
              className="w-full rounded-lg bg-[#00D4AA] py-2.5 font-medium text-gray-950 transition-colors hover:bg-[#00D4AA]/90 focus:outline-none focus:ring-2 focus:ring-[#00D4AA] focus:ring-offset-2 focus:ring-offset-gray-900 disabled:cursor-not-allowed disabled:opacity-50"
            >
              {isLoading ? 'Signing in...' : 'Sign in'}
            </button>
          </form>

          <p className="mt-6 text-center text-sm text-gray-500">
            Don't have an account?{' '}
            <a href="#" className="text-[#00D4AA] hover:underline">
              Contact sales
            </a>
          </p>
        </div>

        <p className="mt-4 text-center text-xs text-gray-600">
          Protected by industry-standard encryption. By signing in, you agree to our{' '}
          <a href="#" className="text-gray-500 hover:text-gray-400">
            Terms of Service
          </a>{' '}
          and{' '}
          <a href="#" className="text-gray-500 hover:text-gray-400">
            Privacy Policy
          </a>
          .
        </p>
      </div>
    </div>
  );
}
