import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import { api } from './client';
import * as mock from './mock';
import type {
  LoginCredentials
} from '../types';

// Enable mock mode via environment variable - defaults to true in dev, false in prod
const USE_MOCK = import.meta.env.VITE_USE_MOCK !== 'false';

// Auth hooks
export function useLogin() {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async (credentials: LoginCredentials) => {
      if (USE_MOCK) {
        await new Promise(resolve => setTimeout(resolve, 500));
        return { user: mock.mockUser, tokens: { access_token: 'mock-token', refresh_token: 'mock-refresh', expires_in: 3600 } };
      }
      return api.login(credentials);
    },
    onSuccess: (data) => {
      api.setToken(data.tokens.access_token);
      queryClient.setQueryData(['user'], data.user);
    }
  });
}

export function useUser() {
  return useQuery({
    queryKey: ['user'],
    queryFn: async () => {
      if (USE_MOCK) {
        return mock.mockUser;
      }
      // In real implementation, this would validate token and get user
      return null;
    },
    staleTime: Infinity
  });
}

// Dashboard hooks
export function useMorningBrief(operatorId?: string) {
  return useQuery({
    queryKey: ['morningBrief', operatorId],
    queryFn: async () => {
      if (USE_MOCK) {
        await new Promise(resolve => setTimeout(resolve, 300));
        return mock.mockMorningBrief;
      }
      return api.getMorningBrief(operatorId);
    },
    refetchInterval: 5 * 60 * 1000 // Refetch every 5 minutes
  });
}

// Price hooks
export function usePrices(params?: { 
  sku?: string; 
  category?: string; 
  operator_id?: string;
  page?: number;
  limit?: number;
  sort_by?: string;
  order?: 'asc' | 'desc';
}) {
  return useQuery({
    queryKey: ['prices', params],
    queryFn: async () => {
      if (USE_MOCK) {
        await new Promise(resolve => setTimeout(resolve, 300));
        let filtered = [...mock.mockPrices];
        
        if (params?.sku) {
          filtered = filtered.filter(p => p.product.sku.toLowerCase().includes(params.sku!.toLowerCase()));
        }
        if (params?.category) {
          filtered = filtered.filter(p => p.product.category === params.category);
        }
        if (params?.operator_id) {
          filtered = filtered.filter(p => p.operator_id === params.operator_id);
        }
        
        // Sorting
        if (params?.sort_by) {
          filtered.sort((a, b) => {
            let aVal: number | string;
            let bVal: number | string;
            
            switch (params.sort_by) {
              case 'price':
                aVal = a.price;
                bVal = b.price;
                break;
              case 'product_name':
                aVal = a.product.name;
                bVal = b.product.name;
                break;
              case 'brand':
                aVal = a.product.brand;
                bVal = b.product.brand;
                break;
              case 'scraped_at':
                aVal = a.scraped_at;
                bVal = b.scraped_at;
                break;
              default:
                aVal = a.scraped_at;
                bVal = b.scraped_at;
            }
            
            if (typeof aVal === 'string') {
              return params.order === 'asc' 
                ? aVal.localeCompare(bVal as string)
                : (bVal as string).localeCompare(aVal);
            }
            return params.order === 'asc' 
              ? (aVal as number) - (bVal as number)
              : (bVal as number) - (aVal as number);
          });
        }
        
        return mock.createPaginatedResponse(filtered, params?.page, params?.limit);
      }
      return api.getPrices(params);
    }
  });
}

export function usePriceMovers(params?: { hours?: number; limit?: number; direction?: 'up' | 'down' | 'both' }) {
  return useQuery({
    queryKey: ['priceMovers', params],
    queryFn: async () => {
      if (USE_MOCK) {
        await new Promise(resolve => setTimeout(resolve, 200));
        let filtered = [...mock.mockPriceMovers];
        
        if (params?.direction === 'up') {
          filtered = filtered.filter(m => m.percent_change > 0);
        } else if (params?.direction === 'down') {
          filtered = filtered.filter(m => m.percent_change < 0);
        }
        
        if (params?.limit) {
          filtered = filtered.slice(0, params.limit);
        }
        
        return { movers: filtered };
      }
      return api.getPriceMovers(params);
    },
    refetchInterval: 5 * 60 * 1000
  });
}

