// Alert hooks
export function useAlerts(params?: {
  operator_id?: string;
  type?: string;
  read?: boolean;
  severity?: string;
  page?: number;
  limit?: number;
}) {
  return useQuery({
    queryKey: ['alerts', params],
    queryFn: async () => {
      if (USE_MOCK) {
        await new Promise(resolve => setTimeout(resolve, 300));
        let filtered = [...mock.mockAlerts];
        
        if (params?.type) {
          filtered = filtered.filter(a => a.type === params.type);
        }
        if (params?.severity) {
          filtered = filtered.filter(a => a.severity === params.severity);
        }
        if (params?.read !== undefined) {
          filtered = filtered.filter(a => a.read === params.read);
        }
        
        return mock.createPaginatedResponse(filtered, params?.page, params?.limit);
      }
      return api.getAlerts(params);
    }
  });
}

