import { Routes, Route, Navigate } from 'react-router-dom';
import MorningBrief from './pages/MorningBrief';
import PriceWire from './pages/PriceWire';
import ThreatRadar from './pages/ThreatRadar';
import Recommendations from './pages/Recommendations';
import Alerts from './pages/Alerts';
import Login from './pages/Login';
import Settings from './pages/Settings';
import { AuthGuard } from './components/AuthGuard';

function App() {
  return (
    <Routes>
      <Route path="/login" element={<Login />} />
      <Route path="/" element={<AuthGuard><MorningBrief /></AuthGuard>} />
      <Route path="/prices" element={<AuthGuard><PriceWire /></AuthGuard>} />
      <Route path="/threats" element={<AuthGuard><ThreatRadar /></AuthGuard>} />
      <Route path="/recommendations" element={<AuthGuard><Recommendations /></AuthGuard>} />
      <Route path="/alerts" element={<AuthGuard><Alerts /></AuthGuard>} />
      <Route path="/settings" element={<AuthGuard><Settings /></AuthGuard>} />
      <Route path="*" element={<Navigate to="/" replace />} />
    </Routes>
  );
}

export default App;
