import { Bell, Search } from 'lucide-react';
import { useNavigate } from 'react-router-dom';

interface TopBarProps {
  title: string;
  unreadAlerts?: number;
}

export function TopBar({ title, unreadAlerts = 0 }: TopBarProps) {
  const navigate = useNavigate();

  return (
    <header className="sticky top-0 z-30 flex h-16 items-center justify-between border-b border-gray-800 bg-gray-950/80 px-4 backdrop-blur lg:px-6">
      {/* Title - adjusted for mobile menu button */}
      <div className="ml-12 lg:ml-0">
        <h1 className="text-xl font-semibold text-gray-100">{title}</h1>
      </div>

      {/* Actions */}
      <div className="flex items-center gap-3">
        {/* Search */}
        <div className="relative hidden sm:block">
          <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-500" />
          <input
            type="text"
            placeholder="Search..."
            className="h-9 w-64 rounded-lg border border-gray-800 bg-gray-900 pl-9 pr-4 text-sm text-gray-100 placeholder:text-gray-500 focus:border-glass-400 focus:outline-none"
          />
        </div>

        {/* Alerts */}
        <button
          onClick={() => navigate('/alerts')}
          className="relative rounded-lg p-2 text-gray-400 transition-colors hover:bg-gray-800 hover:text-gray-100"
        >
          <Bell size={20} />
          {unreadAlerts > 0 && (
            <span className="absolute -right-0.5 -top-0.5 flex h-5 w-5 items-center justify-center rounded-full bg-red-500 text-xs font-medium text-white">
              {unreadAlerts > 9 ? '9+' : unreadAlerts}
            </span>
          )}
        </button>
      </div>
    </header>
  );
}
