// Alert Item
function AlertItem({ alert }: { alert: { id: string; severity: string; title: string; summary: string; read: boolean; created_at: string } }) {
  const severityConfig = {
    critical: { icon: AlertCircle, color: 'text-red-400', bg: 'bg-red-500/10' },
    high: { icon: AlertTriangle, color: 'text-amber-400', bg: 'bg-amber-500/10' },
    medium: { icon: Info, color: 'text-blue-400', bg: 'bg-blue-500/10' },
    low: { icon: Info, color: 'text-gray-400', bg: 'bg-gray-500/10' },
    info: { icon: Info, color: 'text-gray-400', bg: 'bg-gray-500/10' }
  };
  
  const config = severityConfig[alert.severity as keyof typeof severityConfig] || severityConfig.info;
  const Icon = config.icon;
  
  return (
    <div className={cn(
      'flex items-start gap-3 rounded-lg border p-3 transition-colors',
      alert.read ? 'border-gray-800 bg-gray-900/50' : 'border-gray-700 bg-gray-900'
    )}>
      <div className={cn('mt-0.5 flex h-8 w-8 items-center justify-center rounded-lg', config.bg)}>
        <Icon className={cn('h-4 w-4', config.color)} />
      </div>
      <div className="min-w-0 flex-1">
        <div className="flex items-center gap-2">
          <p className={cn('text-sm font-medium', alert.read ? 'text-gray-400' : 'text-gray-100')}>
            {alert.title}
          </p>
          {!alert.read && (
            <span className="h-2 w-2 rounded-full bg-[#00D4AA]" />
          )}
        </div>
        <p className="text-xs text-gray-500 line-clamp-1">{alert.summary}</p>
        <p className="mt-1 text-xs text-gray-600">
          {new Date(alert.created_at).toLocaleDateString('en-US', { month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit' })}
        </p>
      </div>
    </div>
  );
}

// Query param constants for stable React Query cache keys
