import { useState, useMemo } from 'react';
import { 
  Filter, 
  Download, 
  ArrowUp, 
  ArrowDown, 
  Search,
  ChevronLeft,
  ChevronRight,
  TrendingUp,
  TrendingDown,
  ArrowUpRight,
  ArrowDownRight,
  AlertCircle,
  RefreshCw
} from 'lucide-react';
import { clsx, type ClassValue } from 'clsx';
import { twMerge } from 'tailwind-merge';
import { AreaChart, Area, XAxis, YAxis, ResponsiveContainer, Tooltip } from 'recharts';
import { DashboardLayout } from '../layouts/DashboardLayout';
import { usePrices, usePriceMovers } from '../api/hooks';
import type { Price, PriceMover } from '../types';

function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

// Generate deterministic sparkline data based on price seed
function generateSparklineData(basePrice: number, days: number = 7): { label: string; value: number }[] {
  const data: { label: string; value: number }[] = [];
  let currentPrice = basePrice * 0.9;
  
  // Use basePrice as seed for deterministic pseudo-randomness
  const seed = Math.floor(basePrice * 100) % 1000;
  
  for (let i = 0; i < days; i++) {
    // Deterministic "random" based on seed and index
    const pseudoRandom = ((seed + i * 37) % 100) / 100;
    currentPrice += (pseudoRandom - 0.4) * (basePrice * 0.1);
    currentPrice = Math.max(basePrice * 0.7, Math.min(basePrice * 1.2, currentPrice));
    data.push({
      label: `Day ${i + 1}`,
      value: parseFloat(currentPrice.toFixed(2))
    });
  }
  
  return data;
}

// Sparkline Component
function Sparkline({ data, trend }: { data: { label: string; value: number }[]; trend: 'up' | 'down' }) {
  const color = trend === 'up' ? '#00D4AA' : '#ef4444';
  
  return (
    <div className="h-10 w-24">
      <ResponsiveContainer width="100%" height="100%">
        <AreaChart data={data}>
          <defs>
            <linearGradient id={`spark-${trend}`} x1="0" y1="0" x2="0" y2="1">
              <stop offset="0%" stopColor={color} stopOpacity={0.3} />
              <stop offset="100%" stopColor={color} stopOpacity={0} />
            </linearGradient>
          </defs>
          <Area
            type="monotone"
            dataKey="value"
            stroke={color}
            strokeWidth={1.5}
            fill={`url(#spark-${trend})`}
          />
        </AreaChart>
      </ResponsiveContainer>
    </div>
  );
}

// Price Mover Card Component
function PriceMoverCard({ mover }: { mover: PriceMover }) {
  const isIncrease = mover.percent_change > 0;
  const sparkData = generateSparklineData(mover.current_price, 7);
  
  return (
    <div className="rounded-lg border border-gray-800 bg-gray-900 p-4 transition-colors hover:border-gray-700">
      <div className="flex items-start justify-between">
        <div className="flex-1 min-w-0">
          <p className="truncate text-sm font-medium text-gray-100">{mover.sku}</p>
          <p className="text-xs text-gray-500">{mover.operator_name}</p>
          <div className="mt-2 flex items-center gap-2">
            <span className={cn(
              'flex items-center gap-1 text-sm font-bold',
              isIncrease ? 'text-emerald-400' : 'text-red-400'
            )}>
              {isIncrease ? <ArrowUpRight size={14} /> : <ArrowDownRight size={14} />}
              {Math.abs(mover.percent_change).toFixed(2)}%
            </span>
            <span className="text-xs text-gray-500">
              ${mover.previous_price.toFixed(2)}  ${mover.current_price.toFixed(2)}
            </span>
          </div>
        </div>
        <Sparkline data={sparkData} trend={isIncrease ? 'up' : 'down'} />
      </div>
    </div>
  );
}

type SortField = 'sku' | 'brand' | 'category' | 'price' | 'operator' | 'scraped_at';
type SortOrder = 'asc' | 'desc';

const categories = [
  { value: '', label: 'All Categories' },
  { value: 'flower', label: 'Flower' },
  { value: 'pre-roll', label: 'Pre-Roll' },
  { value: 'vape', label: 'Vape' },
  { value: 'concentrate', label: 'Concentrate' },
  { value: 'edible', label: 'Edible' },
  { value: 'tincture', label: 'Tincture' },
  { value: 'topical', label: 'Topical' }
];

// SortIcon component - defined at module level for stable identity
interface SortIconProps {
  field: SortField;
  currentSortField: SortField;
  currentSortOrder: SortOrder;
}

function SortIcon({ field, currentSortField, currentSortOrder }: SortIconProps) {
  if (currentSortField !== field) return <ArrowUp size={14} className="text-gray-600" />;
  return currentSortOrder === 'asc'
    ? <ArrowUp size={14} className="text-[#00D4AA]" />
    : <ArrowDown size={14} className="text-[#00D4AA]" />;
}

// Query params constants
const PRICE_MOVERS_PARAMS = { limit: 6, direction: 'both' as const };

export default function PriceWire() {
  // Filters
  const [categoryFilter, setCategoryFilter] = useState('');
  const [brandSearch, setBrandSearch] = useState('');
  const [skuSearch, setSkuSearch] = useState('');

  // Sorting
  const [sortField, setSortField] = useState<SortField>('scraped_at');
  const [sortOrder, setSortOrder] = useState<SortOrder>('desc');

  // Pagination
  const [page, setPage] = useState(1);
  const limit = 10;

  // Memoized query params for stable React Query cache keys
  const pricesQueryParams = useMemo(() => ({
    category: categoryFilter || undefined,
    page,
    limit,
    sort_by: sortField,
    order: sortOrder
  }), [categoryFilter, page, limit, sortField, sortOrder]);

  // Fetch data with error handling
  const { data: pricesData, isLoading: pricesLoading, error: pricesError, refetch: refetchPrices } = usePrices(pricesQueryParams);
  const { data: moversData, isLoading: moversLoading, error: moversError } = usePriceMovers(PRICE_MOVERS_PARAMS);

  // Filter by brand and SKU locally (since API may not support partial matching)
  const filteredPrices = useMemo(() => {
    if (!pricesData?.data) return [];
    return pricesData.data.filter(price => {
      const matchesBrand = !brandSearch || 
        price.product.brand.toLowerCase().includes(brandSearch.toLowerCase());
      const matchesSku = !skuSearch || 
        price.product.sku.toLowerCase().includes(skuSearch.toLowerCase());
      return matchesBrand && matchesSku;
    });
  }, [pricesData, brandSearch, skuSearch]);

  const handleSort = (field: SortField) => {
    if (sortField === field) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortOrder('asc');
    }
    setPage(1);
  };

  const handleExport = () => {
    if (!filteredPrices.length) return;

    const csv = [
      ['SKU', 'Brand', 'Category', 'Product', 'Price', 'Operator', 'Availability', 'Last Updated'].join(','),
      ...filteredPrices.map(p => [
        p.product.sku,
        p.product.brand,
        p.product.category,
        p.product.name,
        p.price.toFixed(2),
        p.operator.name,
        p.availability,
        new Date(p.scraped_at).toISOString()
      ].join(','))
    ].join('\n');

    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `prices-${new Date().toISOString().split('T')[0]}.csv`;
    a.click();
    URL.revokeObjectURL(url);
  };

  const pagination = pricesData?.pagination;
  const hasError = pricesError || moversError;

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-100">Price Wire</h1>
            <p className="mt-1 text-gray-400">
              Real-time pricing intelligence across Massachusetts operators
            </p>
          </div>
          <button
            onClick={handleExport}
            disabled={!filteredPrices.length}
            className="inline-flex items-center gap-2 rounded-lg border border-gray-700 bg-gray-800 px-4 py-2 text-sm font-medium text-gray-300 transition-colors hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <Download className="h-4 w-4" />
            Export CSV
          </button>
        </div>

        {/* Error State */}
        {hasError && (
          <div className="rounded-lg border border-red-500/30 bg-red-500/10 p-4">
            <div className="flex items-center gap-3">
              <AlertCircle className="h-5 w-5 text-red-400" />
              <div className="flex-1">
                <p className="text-sm font-medium text-red-400">Failed to load data</p>
                <p className="text-xs text-gray-500">
                  {pricesError instanceof Error ? pricesError.message : 'An error occurred while loading price data'}
                </p>
              </div>
              <button
                onClick={() => refetchPrices()}
                className="inline-flex items-center gap-1.5 rounded-lg border border-gray-700 bg-gray-800 px-3 py-1.5 text-sm font-medium text-gray-300 transition-colors hover:bg-gray-700"
              >
                <RefreshCw className="h-3.5 w-3.5" />
                Retry
              </button>
            </div>
          </div>
        )}

        {/* Price Movers Section */}
        <div>
          <div className="mb-3 flex items-center gap-2">
            <TrendingUp className="h-5 w-5 text-[#00D4AA]" />
            <h2 className="text-lg font-semibold text-gray-100">Price Movers (24h)</h2>
          </div>
          {moversLoading ? (
            <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
              {[...Array(6)].map((_, i) => (
                <div key={i} className="h-24 animate-pulse rounded-lg bg-gray-800" />
              ))}
            </div>
          ) : (
            <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
              {moversData?.movers.map((mover, idx) => (
                <PriceMoverCard key={`${mover.sku}-${idx}`} mover={mover} />
              ))}
            </div>
          )}
        </div>

        {/* Filters */}
        <div className="rounded-lg border border-gray-800 bg-gray-900 p-4">
          <div className="flex items-center gap-2 mb-3">
            <Filter className="h-4 w-4 text-gray-400" />
            <span className="text-sm font-medium text-gray-400">Filters</span>
          </div>
          <div className="grid gap-4 sm:grid-cols-3">
            <div>
              <label className="mb-1 block text-xs text-gray-500">Category</label>
              <select
                value={categoryFilter}
                onChange={(e) => {
                  setCategoryFilter(e.target.value);
                  setPage(1);
                }}
                className="w-full rounded-lg border border-gray-700 bg-gray-800 px-3 py-2 text-sm text-gray-100 focus:border-[#00D4AA] focus:outline-none"
              >
                {categories.map(cat => (
                  <option key={cat.value} value={cat.value}>{cat.label}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="mb-1 block text-xs text-gray-500">Brand Search</label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-500" />
                <input
                  type="text"
                  placeholder="Search brands..."
                  value={brandSearch}
                  onChange={(e) => {
                    setBrandSearch(e.target.value);
                    setPage(1);
                  }}
                  className="w-full rounded-lg border border-gray-700 bg-gray-800 pl-9 pr-3 py-2 text-sm text-gray-100 placeholder:text-gray-500 focus:border-[#00D4AA] focus:outline-none"
                />
              </div>
            </div>
            <div>
              <label className="mb-1 block text-xs text-gray-500">SKU Search</label>
              <div className="relative">
                <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-500" />
                <input
                  type="text"
                  placeholder="Search SKUs..."
                  value={skuSearch}
                  onChange={(e) => {
                    setSkuSearch(e.target.value);
                    setPage(1);
                  }}
                  className="w-full rounded-lg border border-gray-700 bg-gray-800 pl-9 pr-3 py-2 text-sm text-gray-100 placeholder:text-gray-500 focus:border-[#00D4AA] focus:outline-none"
                />
              </div>
            </div>
          </div>
        </div>

        {/* Data Table */}
        <div className="overflow-x-auto rounded-lg border border-gray-800">
          <table className="w-full text-left text-sm">
            <thead className="bg-gray-800/50">
              <tr>
                <th
                  className="cursor-pointer px-4 py-3 font-medium text-gray-400 hover:text-gray-300"
                  onClick={() => handleSort('sku')}
                >
                  <div className="flex items-center gap-1">
                    SKU
                    <SortIcon field="sku" currentSortField={sortField} currentSortOrder={sortOrder} />
                  </div>
                </th>
                <th
                  className="cursor-pointer px-4 py-3 font-medium text-gray-400 hover:text-gray-300"
                  onClick={() => handleSort('brand')}
                >
                  <div className="flex items-center gap-1">
                    Brand
                    <SortIcon field="brand" currentSortField={sortField} currentSortOrder={sortOrder} />
                  </div>
                </th>
                <th
                  className="cursor-pointer px-4 py-3 font-medium text-gray-400 hover:text-gray-300"
                  onClick={() => handleSort('category')}
                >
                  <div className="flex items-center gap-1">
                    Category
                    <SortIcon field="category" currentSortField={sortField} currentSortOrder={sortOrder} />
                  </div>
                </th>
                <th
                  className="cursor-pointer px-4 py-3 font-medium text-gray-400 hover:text-gray-300"
                  onClick={() => handleSort('price')}
                >
                  <div className="flex items-center gap-1">
                    Price
                    <SortIcon field="price" currentSortField={sortField} currentSortOrder={sortOrder} />
                  </div>
                </th>
                <th
                  className="cursor-pointer px-4 py-3 font-medium text-gray-400 hover:text-gray-300"
                  onClick={() => handleSort('operator')}
                >
                  <div className="flex items-center gap-1">
                    Operator
                    <SortIcon field="operator" currentSortField={sortField} currentSortOrder={sortOrder} />
                  </div>
                </th>
                <th
                  className="cursor-pointer px-4 py-3 font-medium text-gray-400 hover:text-gray-300"
                  onClick={() => handleSort('scraped_at')}
                >
                  <div className="flex items-center gap-1">
                    Last Updated
                    <SortIcon field="scraped_at" currentSortField={sortField} currentSortOrder={sortOrder} />
                  </div>
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-800">
              {pricesLoading ? (
                [...Array(5)].map((_, i) => (
                  <tr key={i}>
                    <td colSpan={6} className="px-4 py-4">
                      <div className="h-4 animate-pulse rounded bg-gray-800" />
                    </td>
                  </tr>
                ))
              ) : filteredPrices.length === 0 ? (
                <tr>
                  <td colSpan={6} className="px-4 py-8 text-center text-gray-500">
                    No prices found matching your filters.
                  </td>
                </tr>
              ) : (
                filteredPrices.map((price: Price) => (
                  <tr key={price.id} className="hover:bg-gray-800/30">
                    <td className="px-4 py-3">
                      <div>
                        <p className="font-medium text-gray-100">{price.product.sku}</p>
                        <p className="text-xs text-gray-500">{price.product.name}</p>
                      </div>
                    </td>
                    <td className="px-4 py-3 text-gray-300">{price.product.brand}</td>
                    <td className="px-4 py-3">
                      <span className="inline-flex rounded-full bg-gray-800 px-2 py-0.5 text-xs capitalize text-gray-400">
                        {price.product.category}
                      </span>
                    </td>
                    <td className="px-4 py-3">
                      <span className={cn(
                        'font-medium',
                        price.promo_flag ? 'text-[#00D4AA]' : 'text-gray-100'
                      )}>
                        ${price.price.toFixed(2)}
                        {price.promo_flag && (
                          <span className="ml-1 text-xs">*</span>
                        )}
                      </span>
                    </td>
                    <td className="px-4 py-3 text-gray-300">{price.operator.name}</td>
                    <td className="px-4 py-3 text-gray-400">
                      {new Date(price.scraped_at).toLocaleString('en-US', {
                        month: 'short',
                        day: 'numeric',
                        hour: '2-digit',
                        minute: '2-digit'
                      })}
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        {pagination && (
          <div className="flex items-center justify-between text-sm">
            <span className="text-gray-500">
              Showing {((page - 1) * limit) + 1} - {Math.min(page * limit, pagination.total)} of {pagination.total} products
            </span>
            <div className="flex gap-2">
              <button
                onClick={() => setPage(p => Math.max(1, p - 1))}
                disabled={!pagination.has_prev}
                className="flex items-center gap-1 rounded-lg border border-gray-700 bg-gray-800 px-3 py-1.5 text-gray-300 transition-colors hover:bg-gray-700 disabled:cursor-not-allowed disabled:opacity-50"
              >
                <ChevronLeft className="h-4 w-4" />
                Previous
              </button>
              <span className="flex items-center px-3 text-gray-400">
                Page {page} of {pagination.total_pages}
              </span>
              <button
                onClick={() => setPage(p => p + 1)}
                disabled={!pagination.has_next}
                className="flex items-center gap-1 rounded-lg border border-gray-700 bg-gray-800 px-3 py-1.5 text-gray-300 transition-colors hover:bg-gray-700 disabled:cursor-not-allowed disabled:opacity-50"
              >
                Next
                <ChevronRight className="h-4 w-4" />
              </button>
            </div>
          </div>
        )}
      </div>
    </DashboardLayout>
  );
}
