import type {
  Operator,
  Source,
  Price,
  PriceMover,
  Threat,
  ThreatSummary,
  Recommendation,
  Watchlist,
  Alert,
  MorningBrief,
  PaginatedResponse,
  AuthTokens,
  LoginCredentials,
  User
} from '../types';

const API_BASE_URL = import.meta.env.VITE_API_URL || '/api/v1';

class ApiClient {
  private baseUrl: string;
  private token: string | null = null;

  constructor(baseUrl: string = API_BASE_URL) {
    this.baseUrl = baseUrl;
  }

  setToken(token: string) {
    this.token = token;
  }

  clearToken() {
    this.token = null;
  }

  private async request<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<T> {
    const url = `${this.baseUrl}${endpoint}`;
    const headers: Record<string, string> = {
      'Content-Type': 'application/json',
      ...((options.headers as Record<string, string>) || {})
    };

    if (this.token) {
      headers['Authorization'] = `Bearer ${this.token}`;
    }

    const response = await fetch(url, {
      ...options,
      headers
    });

    if (!response.ok) {
      const error = await response.json().catch(() => ({ error: { message: 'Unknown error' } }));
      throw new Error(error.error?.message || `HTTP ${response.status}`);
    }

    if (response.status === 204) {
      return undefined as T;
    }

    return response.json();
  }

  // Auth
  async login(credentials: LoginCredentials): Promise<{ user: User; tokens: AuthTokens }> {
    const response = await this.request<{ user: User } & AuthTokens>('/auth/login', {
      method: 'POST',
      body: JSON.stringify(credentials)
    });
    return {
      user: response.user,
      tokens: {
        access_token: response.access_token,
        refresh_token: response.refresh_token,
        expires_in: response.expires_in
      }
    };
  }

  async refreshToken(refreshToken: string): Promise<AuthTokens> {
    return this.request<AuthTokens>('/auth/refresh', {
      method: 'POST',
      body: JSON.stringify({ refresh_token: refreshToken })
    });
  }

  // Operators
  async getOperators(params?: { page?: number; limit?: number; city?: string; state?: string }): Promise<PaginatedResponse<Operator>> {
    const query = new URLSearchParams();
    if (params?.page) query.set('page', params.page.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    if (params?.city) query.set('city', params.city);
    if (params?.state) query.set('state', params.state);
    return this.request(`/operators?${query.toString()}`);
  }

  async getOperator(id: string): Promise<Operator> {
    return this.request(`/operators/${id}`);
  }

  // Sources
  async getSources(params?: { operator_id?: string; source_type?: string }): Promise<PaginatedResponse<Source>> {
    const query = new URLSearchParams();
    if (params?.operator_id) query.set('operator_id', params.operator_id);
    if (params?.source_type) query.set('source_type', params.source_type);
    return this.request(`/sources?${query.toString()}`);
  }

  // Prices
  async getPrices(params?: { 
    sku?: string; 
    category?: string; 
    operator_id?: string;
    page?: number; 
    limit?: number;
    sort_by?: string;
    order?: 'asc' | 'desc';
  }): Promise<PaginatedResponse<Price>> {
    const query = new URLSearchParams();
    if (params?.sku) query.set('sku', params.sku);
    if (params?.category) query.set('category', params.category);
    if (params?.operator_id) query.set('operator_id', params.operator_id);
    if (params?.page) query.set('page', params.page.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    if (params?.sort_by) query.set('sort_by', params.sort_by);
    if (params?.order) query.set('order', params.order);
    return this.request(`/prices?${query.toString()}`);
  }

  async getPriceMovers(params?: { hours?: number; limit?: number; direction?: 'up' | 'down' | 'both' }): Promise<{ movers: PriceMover[] }> {
    const query = new URLSearchParams();
    if (params?.hours) query.set('hours', params.hours.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    if (params?.direction) query.set('direction', params.direction);
    return this.request(`/prices/movers?${query.toString()}`);
  }

  // Threats
  async getThreats(params?: {
    lat?: number;
    lon?: number;
    radius?: number;
    type?: string;
    severity?: string;
    page?: number;
    limit?: number;
  }): Promise<PaginatedResponse<Threat>> {
    const query = new URLSearchParams();
    if (params?.lat) query.set('lat', params.lat.toString());
    if (params?.lon) query.set('lon', params.lon.toString());
    if (params?.radius) query.set('radius', params.radius.toString());
    if (params?.type) query.set('type', params.type);
    if (params?.severity) query.set('severity', params.severity);
    if (params?.page) query.set('page', params.page.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    return this.request(`/threats?${query.toString()}`);
  }

  async getThreatFeed(params?: { lat?: number; lon?: number; radius?: number }): Promise<{ threats: Threat[]; summary: ThreatSummary }> {
    const query = new URLSearchParams();
    if (params?.lat) query.set('lat', params.lat.toString());
    if (params?.lon) query.set('lon', params.lon.toString());
    if (params?.radius) query.set('radius', params.radius.toString());
    return this.request(`/threats/feed?${query.toString()}`);
  }

  // Recommendations
  async getRecommendations(params?: {
    status?: 'pending' | 'applied' | 'dismissed';
    operator_id?: string;
    page?: number;
    limit?: number;
  }): Promise<PaginatedResponse<Recommendation>> {
    const query = new URLSearchParams();
    if (params?.status) query.set('status', params.status);
    if (params?.operator_id) query.set('operator_id', params.operator_id);
    if (params?.page) query.set('page', params.page.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    return this.request(`/recommendations?${query.toString()}`);
  }

  async applyRecommendation(id: string, notes?: string): Promise<Recommendation> {
    return this.request(`/recommendations/${id}/apply`, {
      method: 'POST',
      body: JSON.stringify({ notes })
    });
  }

  async dismissRecommendation(id: string, reason?: string): Promise<Recommendation> {
    return this.request(`/recommendations/${id}/dismiss`, {
      method: 'POST',
      body: JSON.stringify({ reason })
    });
  }

  // Watchlists
  async getWatchlists(params?: { page?: number; limit?: number }): Promise<PaginatedResponse<Watchlist>> {
    const query = new URLSearchParams();
    if (params?.page) query.set('page', params.page.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    return this.request(`/watchlists?${query.toString()}`);
  }

  async createWatchlist(data: { operator_id: string; product_id: string; alert_threshold_pct: number }): Promise<Watchlist> {
    return this.request('/watchlists', {
      method: 'POST',
      body: JSON.stringify(data)
    });
  }

  async deleteWatchlist(id: string): Promise<void> {
    return this.request(`/watchlists/${id}`, { method: 'DELETE' });
  }

  // Alerts
  async getAlerts(params?: {
    operator_id?: string;
    type?: string;
    read?: boolean;
    severity?: string;
    page?: number;
    limit?: number;
  }): Promise<PaginatedResponse<Alert>> {
    const query = new URLSearchParams();
    if (params?.operator_id) query.set('operator_id', params.operator_id);
    if (params?.type) query.set('type', params.type);
    if (params?.read !== undefined) query.set('read', params.read.toString());
    if (params?.severity) query.set('severity', params.severity);
    if (params?.page) query.set('page', params.page.toString());
    if (params?.limit) query.set('limit', params.limit.toString());
    return this.request(`/alerts?${query.toString()}`);
  }

  async markAlertRead(id: string, read: boolean = true): Promise<Alert> {
    return this.request(`/alerts/${id}`, {
      method: 'PATCH',
      body: JSON.stringify({ read })
    });
  }

  async dismissAlert(id: string, reason?: string): Promise<Alert> {
    return this.request(`/alerts/${id}/dismiss`, {
      method: 'POST',
      body: JSON.stringify({ reason })
    });
  }

  // Dashboard
  async getMorningBrief(operatorId?: string): Promise<MorningBrief> {
    const query = new URLSearchParams();
    if (operatorId) query.set('operator_id', operatorId);
    return this.request(`/dashboard/morning-brief?${query.toString()}`);
  }
}

export const api = new ApiClient();
