import { useState } from 'react';
import { Bell, Check, Trash2, AlertTriangle, TrendingUp, ShieldAlert, Lightbulb } from 'lucide-react';
import { DashboardLayout } from '../layouts/DashboardLayout';

export type AlertType = 'price' | 'threat' | 'recommendation' | 'system';

interface Alert {
  id: string;
  type: AlertType;
  title: string;
  message: string;
  timestamp: string;
  read: boolean;
}

const alertIcons: Record<AlertType, typeof Bell> = {
  price: TrendingUp,
  threat: ShieldAlert,
  recommendation: Lightbulb,
  system: AlertTriangle,
};

const alertColors: Record<AlertType, string> = {
  price: 'text-[#00D4AA] bg-[#00D4AA]/10',
  threat: 'text-red-400 bg-red-500/10',
  recommendation: 'text-purple-400 bg-purple-500/10',
  system: 'text-yellow-400 bg-yellow-500/10',
};

const mockAlerts: Alert[] = [
  {
    id: '1',
    type: 'threat',
    title: 'Critical Price Drop Alert',
    message: 'Curaleaf reduced Wedding Cake pricing by 15% - immediate attention required',
    timestamp: '10 min ago',
    read: false,
  },
  {
    id: '2',
    type: 'price',
    title: 'New Price Detected',
    message: 'Rythm Blue Dream cartridge price changed from $60 to $55',
    timestamp: '1 hour ago',
    read: false,
  },
  {
    id: '3',
    type: 'recommendation',
    title: 'New AI Recommendation',
    message: 'System generated 2 new pricing recommendations with 85%+ confidence',
    timestamp: '2 hours ago',
    read: true,
  },
  {
    id: '4',
    type: 'threat',
    title: 'Competitor Campaign',
    message: 'INSA launched BOGO campaign on vape cartridges',
    timestamp: '3 hours ago',
    read: true,
  },
  {
    id: '5',
    type: 'system',
    title: 'Data Sync Complete',
    message: 'Successfully synced pricing data from 12 operators',
    timestamp: '5 hours ago',
    read: true,
  },
  {
    id: '6',
    type: 'price',
    title: 'Daily Price Report Ready',
    message: 'Your morning briefing with 156 price changes is available',
    timestamp: '8 hours ago',
    read: true,
  },
];

export default function Alerts() {
  const [alerts, setAlerts] = useState<Alert[]>(mockAlerts);
  const [filter, setFilter] = useState<'all' | 'unread'>('all');

  const filteredAlerts = alerts.filter((alert) => {
    if (filter === 'unread') return !alert.read;
    return true;
  });

  const markAsRead = (id: string) => {
    setAlerts((prev) =>
      prev.map((alert) => (alert.id === id ? { ...alert, read: true } : alert))
    );
  };

  const markAllAsRead = () => {
    setAlerts((prev) => prev.map((alert) => ({ ...alert, read: true })));
  };

  const deleteAlert = (id: string) => {
    setAlerts((prev) => prev.filter((alert) => alert.id !== id));
  };

  const unreadCount = alerts.filter((a) => !a.read).length;

  return (
    <DashboardLayout>
      <div className="space-y-6">
        <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-100">Alerts</h1>
            <p className="mt-1 text-gray-400">
              Notifications and alerts from your market monitoring
            </p>
          </div>
          <div className="flex items-center gap-3">
            {unreadCount > 0 && (
              <button
                onClick={markAllAsRead}
                className="inline-flex items-center gap-2 rounded-lg border border-gray-700 bg-gray-800 px-4 py-2 text-sm font-medium text-gray-300 transition-colors hover:bg-gray-700"
              >
                <Check className="h-4 w-4" />
                Mark all read
              </button>
            )}
            <span className="rounded-full bg-[#00D4AA]/10 px-3 py-1 text-sm font-medium text-[#00D4AA]">
              {unreadCount} unread
            </span>
          </div>
        </div>

        <div className="flex gap-2">
          <button
            onClick={() => setFilter('all')}
            className={`rounded-lg px-4 py-2 text-sm font-medium transition-colors ${
              filter === 'all'
                ? 'bg-[#00D4AA] text-gray-950'
                : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
            }`}
          >
            All Alerts
          </button>
          <button
            onClick={() => setFilter('unread')}
            className={`rounded-lg px-4 py-2 text-sm font-medium transition-colors ${
              filter === 'unread'
                ? 'bg-[#00D4AA] text-gray-950'
                : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
            }`}
          >
            Unread
          </button>
        </div>

        <div className="space-y-2">
          {filteredAlerts.length === 0 ? (
            <div className="rounded-lg border border-gray-800 bg-gray-900 p-12 text-center">
              <Bell className="mx-auto h-12 w-12 text-gray-600" />
              <p className="mt-4 text-gray-400">No alerts to display</p>
            </div>
          ) : (
            filteredAlerts.map((alert) => {
              const Icon = alertIcons[alert.type];
              return (
                <div
                  key={alert.id}
                  className={`flex items-start gap-4 rounded-lg border p-4 transition-colors ${
                    alert.read
                      ? 'border-gray-800 bg-gray-900'
                      : 'border-[#00D4AA]/30 bg-[#00D4AA]/5'
                  }`}
                >
                  <div className={`rounded-lg p-2 ${alertColors[alert.type]}`}>
                    <Icon className="h-5 w-5" />
                  </div>
                  <div className="flex-1 min-w-0">
                    <div className="flex items-start justify-between gap-2">
                      <div>
                        <h3 className="font-medium text-gray-100">{alert.title}</h3>
                        <p className="mt-0.5 text-sm text-gray-400">{alert.message}</p>
                        <p className="mt-2 text-xs text-gray-500">{alert.timestamp}</p>
                      </div>
                      <div className="flex gap-1">
                        {!alert.read && (
                          <button
                            onClick={() => markAsRead(alert.id)}
                            className="rounded p-1.5 text-gray-500 hover:bg-gray-800 hover:text-gray-300"
                            title="Mark as read"
                          >
                            <Check className="h-4 w-4" />
                          </button>
                        )}
                        <button
                          onClick={() => deleteAlert(alert.id)}
                          className="rounded p-1.5 text-gray-500 hover:bg-gray-800 hover:text-red-400"
                          title="Delete"
                        >
                          <Trash2 className="h-4 w-4" />
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })
          )}
        </div>
      </div>
    </DashboardLayout>
  );
}
