import { useState } from 'react';
import { RecommendationCard } from '../components/RecommendationCard';
import { DashboardLayout } from '../layouts/DashboardLayout';
import type { Recommendation } from '../types';

const mockRecommendations: Recommendation[] = [
  {
    id: '1',
    operator_id: 'op-1',
    product_id: 'prod-1',
    product: {
      id: 'prod-1',
      sku: 'WEDDING-CAKE-35G',
      name: 'Wedding Cake 3.5g',
      brand: "Nature's Heritage",
      category: 'flower',
      quantity: 3.5,
      unit: 'g',
      created_at: '2024-01-01T00:00:00Z',
    },
    current_price: 50,
    recommended_price: 46,
    confidence: 0.87,
    expected_unit_delta: 0.06,
    expected_margin_delta: 2400,
    status: 'pending',
    rationale: 'Based on competitive analysis, lowering your Wedding Cake pricing from $50 to $46 will align with market rates and potentially increase market share by 5-7%.',
    created_at: '2024-01-15T10:00:00Z',
  },
  {
    id: '2',
    operator_id: 'op-1',
    product_id: 'prod-2',
    product: {
      id: 'prod-2',
      sku: 'BLUE-DREAM-1G',
      name: 'Blue Dream 1g Cart',
      brand: 'Rythm',
      category: 'vape',
      quantity: 1,
      unit: 'g',
      created_at: '2024-01-01T00:00:00Z',
    },
    current_price: 55,
    recommended_price: 55,
    confidence: 0.92,
    expected_unit_delta: 0.15,
    expected_margin_delta: 0,
    status: 'pending',
    rationale: 'Demand signals indicate 23% higher search volume for Blue Dream strain. Current inventory levels may not meet projected demand over the next 2 weeks.',
    created_at: '2024-01-15T11:00:00Z',
  },
  {
    id: '3',
    operator_id: 'op-1',
    product_id: 'prod-3',
    product: {
      id: 'prod-3',
      sku: 'FLASH-SALE',
      name: 'Weekend Flash Sale Bundle',
      brand: 'Multiple',
      category: 'other',
      quantity: 1,
      unit: 'each',
      created_at: '2024-01-01T00:00:00Z',
    },
    current_price: 100,
    recommended_price: 85,
    confidence: 0.78,
    expected_unit_delta: 0.15,
    expected_margin_delta: 1200,
    status: 'pending',
    rationale: 'Competitor analysis shows reduced weekend foot traffic. A targeted 15% flash sale on Friday-Sunday could capture market share from idle competitors.',
    created_at: '2024-01-15T12:00:00Z',
  },
];

export default function Recommendations() {
  const [recommendations, setRecommendations] = useState(mockRecommendations);

  const handleApply = (id: string) => {
    setRecommendations((prev) => prev.filter((r) => r.id !== id));
    alert(`Recommendation ${id} applied!`);
  };

  const handleDismiss = (id: string) => {
    setRecommendations((prev) => prev.filter((r) => r.id !== id));
  };

  return (
    <DashboardLayout>
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-gray-100">Recommendations</h1>
          <p className="mt-1 text-gray-400">
            AI-generated recommendations based on market analysis and competitive intelligence
          </p>
        </div>

        <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
          <div className="rounded-lg border border-gray-800 bg-gray-900 p-4">
            <p className="text-sm text-gray-400">Total Recommendations</p>
            <p className="mt-1 text-2xl font-bold text-gray-100">{recommendations.length}</p>
          </div>
          <div className="rounded-lg border border-gray-800 bg-gray-900 p-4">
            <p className="text-sm text-gray-400">High Confidence (&gt;80%)</p>
            <p className="mt-1 text-2xl font-bold text-[#00D4AA]">
              {recommendations.filter((r) => r.confidence >= 80).length}
            </p>
          </div>
          <div className="rounded-lg border border-gray-800 bg-gray-900 p-4">
            <p className="text-sm text-gray-400">Applied This Week</p>
            <p className="mt-1 text-2xl font-bold text-gray-100">7</p>
          </div>
          <div className="rounded-lg border border-gray-800 bg-gray-900 p-4">
            <p className="text-sm text-gray-400">Avg. Confidence</p>
            <p className="mt-1 text-2xl font-bold text-gray-100">
              {Math.round(
                recommendations.reduce((acc, r) => acc + r.confidence, 0) /
                  recommendations.length
              )}%
            </p>
          </div>
        </div>

        <div className="space-y-4">
          {recommendations.length === 0 ? (
            <div className="rounded-lg border border-gray-800 bg-gray-900 p-12 text-center">
              <p className="text-gray-400">No pending recommendations</p>
              <p className="mt-1 text-sm text-gray-500">
                New recommendations will appear here based on market analysis
              </p>
            </div>
          ) : (
            recommendations.map((rec) => (
              <RecommendationCard
                key={rec.id}
                recommendation={rec}
                onApply={handleApply}
                onDismiss={handleDismiss}
              />
            ))
          )}
        </div>
      </div>
    </DashboardLayout>
  );
}
