import { useState } from 'react';
import { User, Building2, Mail, Bell, Trash2, Plus } from 'lucide-react';
import { DashboardLayout } from '../layouts/DashboardLayout';

interface WatchlistItem {
  id: string;
  name: string;
  type: 'operator' | 'product' | 'category';
}

const mockOperator = {
  name: 'Green Leaf Dispensary',
  license: 'CCC-123456',
  email: 'admin@greenleafma.com',
  role: 'Administrator',
  joinedAt: '2024-01-15',
};

const mockWatchlist: WatchlistItem[] = [
  { id: '1', name: 'Curaleaf', type: 'operator' },
  { id: '2', name: 'Rise', type: 'operator' },
  { id: '3', name: 'INSA', type: 'operator' },
  { id: '4', name: 'Wedding Cake', type: 'product' },
  { id: '5', name: 'Blue Dream', type: 'product' },
  { id: '6', name: 'Flower', type: 'category' },
];

export default function Settings() {
  const [watchlist, setWatchlist] = useState<WatchlistItem[]>(mockWatchlist);
  const [notifications, setNotifications] = useState({
    priceAlerts: true,
    threatAlerts: true,
    dailyBrief: true,
    weeklyReport: true,
  });

  const removeFromWatchlist = (id: string) => {
    setWatchlist((prev) => prev.filter((item) => item.id !== id));
  };

  const getTypeColor = (type: WatchlistItem['type']) => {
    switch (type) {
      case 'operator':
        return 'bg-blue-500/10 text-blue-400';
      case 'product':
        return 'bg-purple-500/10 text-purple-400';
      case 'category':
        return 'bg-orange-500/10 text-orange-400';
      default:
        return 'bg-gray-500/10 text-gray-400';
    }
  };

  return (
    <DashboardLayout>
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-gray-100">Settings</h1>
          <p className="mt-1 text-gray-400">
            Manage your account, watchlist, and notification preferences
          </p>
        </div>

        <div className="grid gap-6 lg:grid-cols-3">
          {/* Profile Section */}
          <div className="lg:col-span-2 space-y-6">
            <div className="rounded-lg border border-gray-800 bg-gray-900 p-6">
              <h2 className="flex items-center gap-2 text-lg font-semibold text-gray-100">
                <User className="h-5 w-5 text-[#00D4AA]" />
                Operator Profile
              </h2>
              <div className="mt-4 grid gap-4 sm:grid-cols-2">
                <div>
                  <label className="text-sm text-gray-500">Business Name</label>
                  <p className="font-medium text-gray-100">{mockOperator.name}</p>
                </div>
                <div>
                  <label className="text-sm text-gray-500">License Number</label>
                  <p className="font-medium text-gray-100">{mockOperator.license}</p>
                </div>
                <div>
                  <label className="text-sm text-gray-500">Email</label>
                  <p className="font-medium text-gray-100">{mockOperator.email}</p>
                </div>
                <div>
                  <label className="text-sm text-gray-500">Role</label>
                  <p className="font-medium text-gray-100">{mockOperator.role}</p>
                </div>
              </div>
              <div className="mt-6">
                <button className="rounded-lg border border-gray-700 bg-gray-800 px-4 py-2 text-sm font-medium text-gray-300 transition-colors hover:bg-gray-700">
                  Edit Profile
                </button>
              </div>
            </div>

            {/* Watchlist Section */}
            <div className="rounded-lg border border-gray-800 bg-gray-900 p-6">
              <div className="flex items-center justify-between">
                <h2 className="flex items-center gap-2 text-lg font-semibold text-gray-100">
                  <Building2 className="h-5 w-5 text-[#00D4AA]" />
                  Watchlist
                </h2>
                <button className="inline-flex items-center gap-1.5 rounded-lg bg-[#00D4AA] px-3 py-1.5 text-sm font-medium text-gray-950 transition-colors hover:bg-[#00D4AA]/90">
                  <Plus className="h-4 w-4" />
                  Add Item
                </button>
              </div>
              <p className="mt-1 text-sm text-gray-400">
                Operators, products, and categories you are actively monitoring
              </p>

              <div className="mt-4 flex flex-wrap gap-2">
                {watchlist.map((item) => (
                  <div
                    key={item.id}
                    className="flex items-center gap-2 rounded-lg border border-gray-800 bg-gray-800 px-3 py-1.5"
                  >
                    <span className={`rounded px-1.5 py-0.5 text-xs ${getTypeColor(item.type)}`}>
                      {item.type}
                    </span>
                    <span className="text-sm text-gray-200">{item.name}</span>
                    <button
                      onClick={() => removeFromWatchlist(item.id)}
                      className="ml-1 text-gray-500 hover:text-red-400"
                    >
                      <Trash2 className="h-3.5 w-3.5" />
                    </button>
                  </div>
                ))}
              </div>

              {watchlist.length === 0 && (
                <p className="mt-4 text-center text-sm text-gray-500">
                  Your watchlist is empty. Add operators or products to monitor.
                </p>
              )}
            </div>
          </div>

          {/* Notifications Section */}
          <div className="rounded-lg border border-gray-800 bg-gray-900 p-6">
            <h2 className="flex items-center gap-2 text-lg font-semibold text-gray-100">
              <Bell className="h-5 w-5 text-[#00D4AA]" />
              Notifications
            </h2>
            <p className="mt-1 text-sm text-gray-400">
              Choose what alerts you want to receive
            </p>

            <div className="mt-6 space-y-4">
              <label className="flex items-center justify-between">
                <span className="text-sm text-gray-300">Price Change Alerts</span>
                <input
                  type="checkbox"
                  checked={notifications.priceAlerts}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    setNotifications((prev) => ({ ...prev, priceAlerts: e.target.checked }))
                  }
                  className="h-4 w-4 rounded border-gray-700 bg-gray-800 text-[#00D4AA] focus:ring-[#00D4AA]"
                />
              </label>
              <label className="flex items-center justify-between">
                <span className="text-sm text-gray-300">Threat Detection</span>
                <input
                  type="checkbox"
                  checked={notifications.threatAlerts}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    setNotifications((prev) => ({ ...prev, threatAlerts: e.target.checked }))
                  }
                  className="h-4 w-4 rounded border-gray-700 bg-gray-800 text-[#00D4AA] focus:ring-[#00D4AA]"
                />
              </label>
              <label className="flex items-center justify-between">
                <span className="text-sm text-gray-300">Daily Morning Brief</span>
                <input
                  type="checkbox"
                  checked={notifications.dailyBrief}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    setNotifications((prev) => ({ ...prev, dailyBrief: e.target.checked }))
                  }
                  className="h-4 w-4 rounded border-gray-700 bg-gray-800 text-[#00D4AA] focus:ring-[#00D4AA]"
                />
              </label>
              <label className="flex items-center justify-between">
                <span className="text-sm text-gray-300">Weekly Report</span>
                <input
                  type="checkbox"
                  checked={notifications.weeklyReport}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                    setNotifications((prev) => ({ ...prev, weeklyReport: e.target.checked }))
                  }
                  className="h-4 w-4 rounded border-gray-700 bg-gray-800 text-[#00D4AA] focus:ring-[#00D4AA]"
                />
              </label>
            </div>

            <div className="mt-6 pt-6 border-t border-gray-800">
              <h3 className="text-sm font-medium text-gray-300">Email Preferences</h3>
              <div className="mt-3 space-y-2">
                <label className="flex items-center gap-2">
                  <Mail className="h-4 w-4 text-gray-500" />
                  <span className="text-sm text-gray-400">admin@greenleafma.com</span>
                </label>
                <button className="text-sm text-[#00D4AA] hover:underline">
                  + Add email address
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </DashboardLayout>
  );
}
